<template>
    <div style="text-align: left">
        <!-- 动作，空文件 -->
        <div class="inline-block left-panel">
            <!-- 动作类型 -->
            <el-select v-model="action.type" placeholder="请选择"
                       style="margin-top: 25px;margin-right: 10px;font-size: 12px"
                       size="small">
                <el-option v-for="item in ruleTypes" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
            </el-select>
        </div>
        <!-- 参数设置- 请求转发 -->
        <div v-if="action.type == 'redirect'" class="inline-block right-panel">
            <div class="action-data">
                <el-input v-model="action.data.target" size="small"
                          :placeholder="redirectPlaceholder">
                </el-input>
            </div>
        </div>
        <!-- 参数设置- 返回自定义数据 el-select的一个bug，当el-select从界面中消失的时候会解绑事件。。所以用v-show -->
        <div v-show="action.type == 'mockData'" class="inline-block right-panel">
            <div class="action-data">
              <span>
                <el-select v-model="action.data.dataId"
                           style="width: 300px" size="small" filterable placeholder="请选择要返回的数据">
                  <el-option
                          v-for="dataentry in dataList"
                          :label="dataentry.name" :key="dataentry.id"
                          :value="dataentry.id">
                  </el-option>
                </el-select>
              </span>
                <span v-if="datafileEntry" style="margin-left: 10px;">
                <el-button type="text" @click="$emit('edit-data-file', datafileEntry)">
                  编辑数据
                </el-button>
              </span>
                <span style="margin-left: 10px;">
                <el-button type="text" @click="$emit('new-data-file')">增加自定义数据</el-button>
              </span>
            </div>
        </div>
        <!-- 设置cookie -->
        <div v-if="action.type == 'addRequestCookie'" class="inline-block right-panel">
            <div class="action-data">
                <el-input v-model="action.data.cookieKey" size="small"
                          placeholder="cookie key" style="display: inline-block;width: 120px;">
                </el-input>
                <el-input v-model="action.data.cookieValue" size="small"
                          placeholder="cookie value" style="display: inline-block;width: 300px;">
                </el-input>
            </div>
        </div>
        <!-- 修改返回内容 -->
        <div v-if="action.type == 'modifyResponse'" class="inline-block right-panel">
            <div class="action-data">
                <el-select v-model="action.data.modifyResponseType" style="width: 260px"
                           size="small" placeholder="请选择修改返回body操作">
                    <el-option v-for="item in modifyResponseType" :key="item.value" :label="item.label"
                               :value="item.value">
                    </el-option>
                </el-select>
                <span v-if="action.data.modifyResponseType == 'returnDataInJsonpStyle'">
                  <el-input v-model="action.data.callbackName" size="small" style="width:200px;"
                            placeholder="jsonp callback参数名">
                  </el-input>
                </span>
            </div>
        </div>
        <!-- 增加请求头 -->
        <div v-if="action.type == 'addRequestHeader'" class="inline-block right-panel">
            <div class="action-data">
                <el-input v-model="action.data.reqHeaderKey" size="small"
                          placeholder="header key" style="display: inline-block;width: 120px;">
                </el-input>
                <el-input v-model="action.data.reqHeaderValue" size="small"
                          placeholder="header value" style="display: inline-block;width: 300px;">
                </el-input>
            </div>
        </div>
        <!-- 增加请求Query -->
        <div v-if="action.type == 'addQuery'" class="inline-block right-panel">
            <div class="action-data">
                <el-input v-model="action.data.queryKey" size="small"
                          placeholder="query key" style="display: inline-block;width: 120px;">
                </el-input>
                <el-input v-model="action.data.queryValue" size="small"
                          placeholder="query value" style="display: inline-block;width: 300px;">
                </el-input>
            </div>
        </div>
        <!-- 增加响应头 -->
        <div v-if="action.type == 'addResponseHeader'" class="inline-block right-panel">
            <div class="action-data">
                <el-input v-model="action.data.resHeaderKey" size="small"
                          placeholder="header key" style="display: inline-block;width: 120px;">
                </el-input>
                <el-input v-model="action.data.resHeaderValue" size="small"
                          placeholder="header value" style="display: inline-block;width: 300px;">
                </el-input>
            </div>
        </div>
        <!-- 脚本修改请求 -->
        <div v-if="action.type == 'scriptModifyRequest'" class="inline-block right-panel">
            <textarea style="width: 500px;height: 90px" v-model="action.data.modifyRequestScript">
            </textarea>
        </div>
        <!-- 脚本修改响应 -->
        <div v-if="action.type == 'scriptModifyResponse'" class="inline-block right-panel">
            <textarea style="width: 500px;height: 90px" v-model="action.data.modifyResponseScript">
            </textarea>
        </div>
    </div>
</template>

<script>
  import find from 'lodash/find';

  export default {
    name: 'action-detail',
    props: ['action', 'ruleTypes', 'dataList', 'userId'],
    data() {
      return {
        modifyResponseType: [
          {value: 'addTimestampToJsCss', label: '将html中的js、css请求加上时间戳'},
          {value: 'returnDataInJsonpStyle', label: '以JSONP的方式返回数据'},
          {value: 'allowCros', label: '增加跨域头部'},
          {value: 'return404', label: '返回404'}
        ]
      };
    },
    computed: {
      datafileEntry() {
        if (this.action.type == "mockData") {
          var finded = find(this.dataList, (entry) => {
            return entry.id == this.action.data.dataId;
          });
          if (!finded) this.action.data.dataId = '';
          return finded;
        }
      },
      redirectPlaceholder() {
        if (this.userId == 'root') {
          return '填写转发路径(远程地址、或者本地地址。远程地址需要以http/https开头)'
        } else {
          return '填写转发路径(必须以http/https开头)'
        }
      }
    },

  };

</script>
<style>
    .inline-block {
        display: inline-block;
    }

    .conditon {
        margin-top: 10px;
    }

    .conditon:after {
        content: "";
        display: block;
        border-bottom: 1px dotted #adadad;
    }

    .action {
        margin-bottom: 10px;
    }

    .left-panel {
        margin-top: 10px;
        height: 80px;
        width: 160px;
        vertical-align: top;
        border-right: 1px dotted #c0c0c0;
    }

    .right-panel {
        width: calc(100% - 170px);
        height: 80px;
        padding-left: 10px;
    }

    .action-data {
        text-align: left;
        margin-top: 35px;

    }

    .action-option {
        margin-top: 5px;
    }
</style>



